.c-footer {
	height: 168px;
	background-color: $footer-background-color;
	display: flex;
	justify-content: space-between;
	&__logo-menu {
		margin-top: $normal-margin;
		margin-left: $large-margin;
		@include respond-to(handhelds) {
			margin-left: 0;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
		@include respond-to(tablets) {
			margin-left: 0;
		}
	}
	&__menu {
		display: inline-block;
		vertical-align: top;
		margin-top: $small-margin;
		margin-left: $normal-margin;
		&-item {
			color: $black;
			font-size: $medium-font;
			line-height: 2.25;
			letter-spacing: $letter-spacing;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
			@include respond-to(handhelds) {
				margin-right: 10px;
			}
			@include respond-to(tablets) {
				margin-right: 20px;
			}
			@include respond-to(medium-screens) {
				margin-right: 20px;
			}
			@include respond-to(wide-screens) { 
				margin-right: $x-normal-margin;
			}
		}
		@include respond-to(handhelds) {
			margin-left: 0;
		}
		@include respond-to(tablets) {
			margin-left: 30px;
		}
	}
	&__social {
		text-align: center;
		display: inline-block;
		vertical-align: top;
		margin-top: 20px;
		&-item {
			width: $small-width;
			height: $small-height;
			display: inline-block;
			vertical-align: top;
			border: 1px solid transparent;
			background-color: transparent;
			outline: none;
			&:hover {
				border-color: $black;
				border-radius: $round-border-radius;
				cursor: pointer;
			}
			&-img {
				height: $social-icon-height;
				width: auto;
			}
		}
		@include respond-to(tablets) {
			display: block;
		}
	}
	&__address {
		font-size: $medium-font;
		letter-spacing: $letter-spacing;
		margin-top: $x-normal-margin;
		margin-right: $large-margin;
		&-city {
			color: $black;
			display: block;
			line-height: 2.25;
		}
		&-street {
			display: block;
			color: $dark-grey;
			line-height: 1.25;
		}
		&-tel {
			display: block;
			color: $dark-grey;
			line-height: 1.25;
		}
		@include respond-to(handhelds) {
			margin-top: 0;
			margin-right: 0;
			padding-bottom: 30px;
		}
		@include respond-to(tablets) {
			margin-right: 0;
			padding-bottom: 30px;
		}
	}
	@include respond-to(handhelds) {
		flex-direction: column;
		align-items: center;
		height: 370px;
	}
	@include respond-to(tablets) {
		flex-direction: column;
		align-items: center;
		height: 325px;
		&__logo-img {
			text-align: center;
		}
	}
}